import React from 'react'
import { graphql, useStaticQuery } from 'gatsby'
import { getImage, GatsbyImage, StaticImage } from "gatsby-plugin-image"
import styled from '@emotion/styled'

import { convertToBgImage } from "gbimage-bridge"
import BackgroundImage from 'gatsby-background-image'

import Box from '../../../components/Box'
import { Media } from '../../../components/ThemeProvider/theme'

const GatsbyBgImage = styled(BackgroundImage)`
height: 100%;
width: 100%;
`

const Banner = () => {
  const { mobile, desktop } = useStaticQuery(
    graphql`
      query {
        mobile: file(relativePath: { eq: "containers/HomePage/Banner/mobile.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 768) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        desktop: file(relativePath: { eq: "containers/HomePage/Banner/desk.jpg" }) {
          childImageSharp {
            fluid(quality: 90, maxWidth: 2560) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
      }
    `
  )
  const imageData = mobile.childImageSharp.fluid
  const imageDeskData = desktop.childImageSharp.fluid

  // Use like this:
  return (
    <Box.Relative height="100%" width="auto">
      <Box as={Media} lessThan="tablet" height="100%">
        <GatsbyBgImage
          fluid={imageData}
        >
        </GatsbyBgImage>
        <Box.FullAbs>
          <StaticImage src="mobile_text.png" alt="越永續，越得意" />
        </Box.FullAbs>
      </Box>
      <Box as={Media} greaterThanOrEqual="tablet" height="100%">
        <GatsbyBgImage
          fluid={imageDeskData}
        >
        </GatsbyBgImage>
      </Box>
    </Box.Relative>
  )
}

export default Banner
